* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --black: #393e46 ;
    --white: #eeeeee;
    --teal: #00adb5;
}

body {
    background-color: var(--teal);
    height: 100vh;
    width: 100%;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    height: 100%;
    width: 100%;
}

#app {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    height: 100%;
    width: 100%;
}

#cvHeader {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;

    background-color: var(--black);
    color: var(--white);
    font-size: 35px;
    margin-bottom: 75px;
    padding: 15px 10px;
    width: 100%;
}

#mainContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 25px;

    /* padding-bottom: 50px; */
    /* height: 100%; */
    padding-left: 30px;
    padding-right: 30px;
    width: 100%;
}

#cvFooter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    background-color: var(--black);
    color: var(--white);
    font-size: 18px;
    margin-top: 75px;
    padding: 5px 0;
    width: 100%;
}

#cvFooter footer small a {
    color: var(--white);
}